<template>
  <div class="max-w-md mx-auto mt-10 p-4 border rounded">
    <div v-if="step === 1">
      <label for="phone" class="block mb-2 font-medium">شماره موبایل:</label>
      <input v-model="phone" id="phone" type="text" class="border p-2 w-full rounded" />

      <div class="mt-4">
        <button
          @click="submitPhone"
          :disabled="submitDisabled || !isValidPhone"
          class="bg-blue-500 text-white px-4 py-2 rounded disabled:opacity-50"
        >
          <span v-if="submitDisabled">ارسال مجدد تا {{ submitCountdown }} ثانیه</span>
          <span v-else>ارسال کد</span>
        </button>
      </div>
    </div>

    <div v-if="step === 2" class="mt-4">
      <div class="flex items-center justify-between">
        <span class="font-bold">کد برای {{ phone }} ارسال شد</span>
        <button @click="editPhone" class="text-sm text-blue-600 underline">ویرایش</button>
      </div>
      <input v-model="code" type="text" placeholder="کد تایید" class="border p-2 mt-4 w-full rounded" />
      
      <div class="mt-4">
        <button
          @click="verifyCode"
          :disabled="code.length !== 6"
          class="bg-green-500 text-white px-4 py-2 rounded disabled:opacity-50"
        >
          تایید
        </button>
      </div>

      <div class="mt-4 text-sm text-gray-600">
        <span v-if="countdown > 0">ارسال مجدد کد تا {{ countdown }} ثانیه دیگر</span>
        <button v-else-if="showSend" @click="resendCode" class="text-blue-600 underline">ارسال مجدد کد</button>
      </div>
    </div>

    <div v-if="message" class="mt-4 p-2 bg-yellow-100 text-yellow-800 rounded">
      {{ message }}
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      phone: '',
      code: '',
      message: '',
      step: 1,
      countdown: 0,
      countdownInterval: null,
      submitDisabled: false,
      submitCountdown: 0,
      submitInterval: null,
      sentOnce: false,
      showSend: false,
    };
  },
  computed: {
    isValidPhone() {
      return /^09\d{9}$/.test(this.phone);
    },
  },
  mounted() {
    const savedPhone = sessionStorage.getItem('phone');
    const sentAt = sessionStorage.getItem('sent_at');
    const submitAt = sessionStorage.getItem('submit_at');
    const sentOnce = sessionStorage.getItem('sentOnce') === '1';
    const showSend = sessionStorage.getItem('showSend') === '1';

    if (savedPhone) {
      this.phone = savedPhone;
    }

    if (sentAt) {
      const elapsed = Math.floor((Date.now() - Number(sentAt)) / 1000);
      if (elapsed < 300) {
        this.step = 2;
        this.startCountdown(120 - elapsed);
      } else {
        sessionStorage.removeItem('sent_at');
      }
    }

    if (submitAt) {
      const submitElapsed = Math.floor((Date.now() - Number(submitAt)) / 1000);
      if (submitElapsed < 60) {
        this.startSubmitLock(60 - submitElapsed);
      }
    }

    if (sentOnce) this.sentOnce = true;
    if (showSend) this.showSend = true;
  },
  methods: {
    submitPhone() {
      if (!this.isValidPhone) return;

      sessionStorage.setItem('phone', this.phone);
      sessionStorage.setItem('sent_at', Date.now().toString());
      sessionStorage.setItem('submit_at', Date.now().toString());
      sessionStorage.setItem('sentOnce', '1');
      sessionStorage.setItem('showSend', '1');

      this.step = 2;
      this.sentOnce = true;
      this.showSend = true;
      this.message = 'کد ارسال شد';
      this.startCountdown();
      this.startSubmitLock();
    },
    verifyCode() {
      if (this.code === '123456') {
        this.message = 'ورود موفق!';
      } else {
        this.message = 'کد اشتباه است.';
      }
    },
    resendCode() {
      sessionStorage.setItem('sent_at', Date.now().toString());
      sessionStorage.setItem('submit_at', Date.now().toString());

      this.message = 'کد مجدداً ارسال شد';
      this.startCountdown();
      this.startSubmitLock();
    },
    editPhone() {
      this.step = 1;
      this.message = '';
      this.code = '';
      this.sentOnce = false;
      this.showSend = false;
      sessionStorage.removeItem('phone');
      sessionStorage.removeItem('sent_at');
      sessionStorage.removeItem('submit_at');
      sessionStorage.removeItem('sentOnce');
      sessionStorage.removeItem('showSend');

      if (this.countdownInterval) clearInterval(this.countdownInterval);
      if (this.submitInterval) clearInterval(this.submitInterval);

      this.startSubmitLock(); // شروع مجدد کنت‌داون
    },
    startCountdown(seconds = 120) {
      this.countdown = seconds;
      if (this.countdownInterval) clearInterval(this.countdownInterval);
      this.countdownInterval = setInterval(() => {
        this.countdown--;
        if (this.countdown <= 0) {
          clearInterval(this.countdownInterval);
          this.showSend = true;
        }
      }, 1000);
    },
    startSubmitLock(seconds = 60) {
      this.submitDisabled = true;
      this.submitCountdown = seconds;
      if (this.submitInterval) clearInterval(this.submitInterval);
      this.submitInterval = setInterval(() => {
        this.submitCountdown--;
        if (this.submitCountdown <= 0) {
          this.submitDisabled = false;
          clearInterval(this.submitInterval);
        }
      }, 1000);
    },
  },
};
</script>

<style scoped>
input {
  direction: ltr;
}
</style>
